<template>
    <div>
        <slot></slot>
    </div>
</template>

<script>
    /**
     * 
     * @module BhPanel
     *
     * @example
     * <caption>html</caption>
     * <bh-panel title='' caption='' tag=''>
     *     <div>
     *         <p></p>
     *         <p></p>
     *         <p></p>
     *         <p></p>
     *     </div>
     * </bh-panel>
     */
    export default {
        /**
         * @property {String} title html
         * @property {String} [caption] html
         * @property {Boolean} [hasBorder=true] 
         * @property {String} [tag]  html
         * @property {String} [toolbar]  html
         * @property {Function} [beforeExpand] 
         * @property {Function} [afterExpand] 
         * @property {Function} [beforeCollapse] 
         * @property {Function} [afterCollapse] 
         */
        props: {
            title: String,
            caption: String,
            tag: String,
            toolbar: String,
            hasBorder: {
                type: Boolean,
                default: true
            },
            beforeExpand: Function,
            afterExpand: Function,
            beforeCollapse: Function,
            afterCollapse: Function
        },
        ready () {
            var self = this;
            var el = $(this.$el);

            el.bhCollapsiblePanel({
                title: self.title,
                caption: self.caption,
                hasBorder: self.hasBorder,
                tag: self.tag,
                toolbar: self.toolbar,
                beforeExpand: self.beforeExpand,
                afterExpand: self.afterExpand,
                beforeCollapse: self.beforeCollapse,
                afterCollapse: self.afterCollapse
            });
        }
    };
</script>



