<template>
    <div></div>
</template>

<script>
    /**
     * EMAP
     * @module EmapDatatable
     *
     * @example
     *     <caption>html</caption>
     *     <emap-datatable :options='options' @edit='edit' @del='del'></emap-datatable>
     * @example
     *     <caption>javascript</caption>
     *      import EmapDatatable from 'components/emap-datatable/emapDatatable.vue'
     *      // 
     *      var getCustomColumns = function() {
     *          var customColumns = [{
     *              colField: 'status',
     *              type: 'tpl',
     *              width: 50,
     *              column: {
     *                  cellsRenderer: function(row, column, value, rowData) {
     *                      return value ? '' : '';
     *                  }
     *              }
     *          }, {
     *              colField: 'name',
     *              type: 'tpl',
     *              width: 300,
     *              column: {
     *                  cellsRenderer: function(row, column, value, rowData) {
     *                      return '<a href="#/bzxq" target="_blank" data-action="save-id" data-wid="' + rowData.WID + '">' + value + '</span>';
     *                  }
     *              }
     *          }];
     *          return customColumns;
     *      };
     *      export default {
     *          data: function() {
     *              return {
     *                  options: {
     *                      pagePath: 'http://localhost:3000/mock/emap/major-model.json',
     *                      action: 'TABLE',
     *                      customColumns: getCustomColumns(),
     *                      checkable: true,
     *                      sortable: true,
     *                      columnsReorder: true,
     *                      operations: {
     *                          title: '',
     *                          width: 100,
     *                          items: [{
     *                              title: '',
     *                              name: 'edit',
     *                              type: 'link'
     *                          }, {
     *                              title: '',
     *                              name: 'del',
     *                              type: 'link'
     *                          }]
     *                      }
     *                  }
     *              }
     *          },
     *          methods: {
     *              edit: function(row) {
     *                  console.log('edit', row);
     *              },
     *              del: function(row) {
     *                  console.log('del', row);
     *              }
     *          }
     *          components: {EmapDatatable}
     *      }
     */

    var defaultOpts = {
        height: null, // fix scrollbar defect
        checkable: false,
        customColumns: [],
        operations: null
    };

    var getOptItem = (row, item) => {
        if (item.type === 'button') {
            return "<button data-name='" + item.name + "' data-row='" + row + "' class='opt-button'> style='padding: 0 5px;'" + item.title + '</button>';
        } else {
            return "<a href='javascript:void(0)' data-name='" + item.name + "' data-row='" + row + "' class='opt-button' style='padding: 0 5px;'>" + item.title + '</a>';
        }
    };

    var getOptColumn = (vm) => {
        return {
            colIndex: 'last',
            type: 'tpl',
            column: {
                width: vm.options.operations.width,
                text: vm.options.operations.title,
                cellsRenderer (row, column, value, rowData) {
                    vm.cachedMap[row] = rowData;
                    var items = vm.options.operations.items;

                    if ($.isFunction(items)) {
                        items = items.call(vm, rowData);
                    }

                    var html = '';
                    $.each(items, (i, item) => {
                        html += getOptItem(row, item);
                    });

                    return html;
                }
            }
        };
    };

    export default {
        data () {
            return {
                cachedMap: {}
            };
        },
        /**
         * @property {Object} options 
         * @property {String} [options.pk=WID] 
         * @property {String} [options.width=100%] 
         * @property {String} [options.height] 
         * @property {String} [options.method=POST] 
         * @property {Boolean} [options.checkable=false] 
         * @property {Boolean} [options.pageable=true] 
         * @property {String} [options.pagerMode=advanced] 'simple'/'advanced'
         * @property {Boolean} [options.serverProcessing=true] 
         * @property {Array} [options.pageSizeOptions=['10', '20', '50', '100']] 
         * @property {String} [options.localization=zh-CN] 
         * @property {Boolean} [options.sortable=false] 
         * @property {String} [options.selectionMode=custom]  'multipleRows'/'singleRow'/'custom'
         * @property {Boolean} [options.enableBrowserSelection=true] 
         * @property {Boolean} [options.columnsResize=true] 
         * @property {Boolean} [options.colHasMinWidth=true] 100px
         * @property {Function} [options.beforeSend] 
         * @property {Boolean} [options.schema=true] schema truecontextPath;contextPath,schema 
         * @property {String} options.contextPath 
         * @property {Number} options.minLineNum 
         * @property {Array} [options.alwaysHide=['WID', 'TBRQ', 'TBLX', 'CZRQ', 'CZZ', 'CZZXM']] 
         * @property {Object} [options.operations] 
         * @property {String} options.operations.title 
         * @property {Object[]} options.operations.items 
         * @property {String} options.operations.items.title 
         * @property {String} options.operations.items.name  dispatch 
         * @property {String} options.operations.items.type 'button'
         */
        props: {
            options: Object
        },
        ready () {
            var self = this;
            var el = $(self.$el);

            var opts = $.extend({}, defaultOpts, self.options);
            var customColumns = opts.customColumns;
            var operations = opts.operations;

            if (opts.checkable) { // 
                customColumns.unshift({
                    colIndex: 0,
                    showCheckAll: true,
                    width: 32,
                    type: 'checkbox'
                });
            }

            if (operations) { // 
                customColumns.push(getOptColumn(self));
            }

            opts.checkable = undefined;
            opts.operations = undefined;

            el.emapdatatable(opts);

            el.on('click', '.opt-button', function (e) {
                var _this = $(this);
                var row = _this.attr('data-row');
                var name = _this.attr('data-name');

                self.$dispatch(name, self.cachedMap[row]);
            });
        },
        methods: {
            /**
             * 
             * @param  {Object} params 
             */
            reload (params) {
                $(this.$el).emapdatatable('reload', params);
            },
            /**
             * checkbox
             *
             * @return {Array} 
             */
            checkedRecords () {
                return $(this.$el).emapdatatable('checkedRecords');
            },
            /**
             * 
             */
            reloadFirstPage () {
                return $(this.$el).emapdatatable('reloadFirstPage');
            },
            /**
             * 
             * @return {Number} 
             */
            getTotalRecords () {
                return $(this.$el).emapdatatable('getTotalRecords');
            },
            /**
             * meta
             * @return {Object} 
             */
            getModel () {
                return $(this.$el).emapdatatable('getModel');
            },
            /**
             * 
             * @return {Object} 
             */
            getResult () {
                return $(this.$el).emapdatatable('getResult');
            },
            /**
             * 
             * @param {Object} params 
             * @param {String} params.root 
             * @param {String} params.app 
             * @param {String} params.module 
             * @param {String} params.page 
             * @param {String} params.action 
             * @param {String} params.colnames 
             * @param {String} params.analyse IImportAnalyse
             * @param {String} params.write IExportWrite
             * @param {String} params.filename 
             */
            selectColumnsExport (params) {
                return $(this.$el).emapdatatable('selectColumnsExport', {
                    type: 'export',
                    params: params
                });
            },
            /**
             * 
             * @param  {Object} action 
             * @param {String} action.type=toggle 'toggle'()/'export'(  )
             * @param {String} action.hanlder 
             * @param {String} action.param action
             */
            selectToShowColumns (action) {
                return $(this.$el).emapdatatable('selectToShowColumns', action);
            },
            /**
             * 
             * @param  {Object} config 
             * @param {String} config.root 
             */
            export (config) {
                return $(this.$el).emapdatatable('export', config);
            }
        },
        beforeDestroy () {
            this.cachedMap = null;
        }
    };
</script>



