<template>
    <div></div>
</template>

<script>
    /**
     * Form
     * @module EmapForm
     * @example
     *     // meta
     *     text
     *     textarea
     *     selecttable
     *     select
     *     multi-select2
     *     tree
     *     multi-tree
     *     date-local
     *     date-ym
     *     date-full
     *     switcher
     *     uploadfile
     *     uploadphoto
     *     uploadsingleimage
     *     uploadmuiltimage
     *     buttonlist
     *     multi-select
     *     div
     *     static
     *     number
     *     number-range
     *     radiolist
     *     checkboxlist
     * @example
     *     <caption>html</caption>
     *     <emap-form :options='options'></emap-form>
     * @example
     *     <caption>javascript</caption>
     *     export default {
     *         data: function() {
     *             return {
     *                 options: {
     *                     pagePath: 'http://res.wisedu.com/fe_components/mock/page_model.json',
     *                     modelName: 'FORM_GROUP'
     *                 }
     *             }
     *         },
     *         ready: function() {
     *             // 
     *             $.bhFormOutline.show({
     *                 offset: {
     *                     right: 16,
     *                     top: 84
     *                 },
     *                 scrollOffsetTop: $("header.bh-header-mini").outerHeight(),
     *                 insertContainer: $(".eform-page")
     *             });
     *             $.bhAffix({
     *                 //
     *                 hostContainer: $(".eform-page"),
     *                 //
     *                 fixedContainer: $("div.bh-form-outline")
     *             });
     *         },
     *         components: {EmapForm}
     *     }
     */
    export default {
        /**
         * @property {Object} options 
         * @property {Boolean} options.pagePath 
         * @property {Boolean} options.modelName 
         * @property {Boolean} [options.readonly=false] 
         * @property {String} [options.model=h] 'h'/'v'(/)
         * @property {String} [options.size=M] 
         * @property {String} [options.cols=3] 
         * @property {String} [options.root] 
         * @property {Boolean} [options.validate=true] 
         * @property {Boolean} [options.renderByGroup=true] 
         * @property {Boolean} [options.autoColumn=true] 
         * @property {String} [options.inputWidth=6] 
         * @property {String} [offsetTop=84] 
         * @property {String} [offsetRight=16] 
         * @property {String} [scrollTop='.bh-header-mini'] 
         * @property {Boolean} [outline=true] 
         * @property {DOMElement} [container]  nulloutlinetrue
         */
        props: {
            options: Object,
            offsetTop: {
                default: 84
            },
            offsetRight: {
                default: 16
            },
            scrollTop: {
                default: $('header.bh-header-mini').outerHeight()
            },
            outline: {
                type: Boolean,
                default: true
            },
            container: {
                default: null
            }
        },
        methods: {
            /**
             * 
             * @param  {Array} ids id
             */
            disableItem (ids) {
                $(this.$el).emapForm('disableItem', ids);
            },
            /**
             * 
             * @param  {Array} ids id
             */
            enableItem (ids) {
                $(this.$el).emapForm('enableItem', ids);
            },
            /**
             * 
             */
            saveUpload () {
                $(this.$el).emapForm('saveUpload');
            },
            /**
             * 
             * @param  {Array} ids id
             */
            showItem (ids) {
                $(this.$el).emapForm('showItem', ids);
            },
            /**
             * 
             * @param  {Array} ids id
             */
            hideItem (ids) {
                $(this.$el).emapForm('hideItem', ids);
            },
            /**
             * 
             * @return {Object} 
             */
            getValue () {
                return $(this.$el).emapForm('getValue');
            },
            /**
             * 
             */
            setValue (val) {
                $(this.$el).emapForm('setValue', val);
            },
            /**
             * 
             */
            reloadValidate () {
                $(this.$el).emapForm('reloadValidate');
            },
            /**
             * 
             * @return {Object} 
             */
            getModel () {
                return $(this.$el).emapForm('getModel');
            },
            /**
             * 
             * @return {Boolean} 
             */
            validate () {
                return $(this.$el).emapValidate('validate');
            },
            /**
             * 
             * 
             */
            clear (val) {
                return $(this.$el).emapForm('clear', val);
            },
            initForm (opts) {
                var datamodel = WIS_EMAP_SERV.getModel(opts.pagePath, opts.modelName, 'form');
                opts.data = datamodel;
                $(this.$el).emapForm(opts);
                this.reloadValidate();
            },
            initOutline () {
                if (this.outline) {
                    if (!this.container) {
                        console.error('emap form  container !');
                        return;
                    }

                    // 
                    $.bhFormOutline.show({
                        offset: {
                            right: this.offsetRight,
                            top: this.offsetTop
                        },
                        scrollOffsetTop: this.scrollTop,
                        insertContainer: $(this.container)
                    });
                    $.bhAffix({
                        // 
                        hostContainer: $(this.container),
                        // 
                        fixedContainer: $('div.bh-form-outline')
                    });
                }
            },
            /**
             * 
             */
            destroy () {
                $(this.$el).emapForm('destroy');
            }
        },
        ready () {
            var opts = this.options;
            this.initForm(opts);
            this.initOutline();

            this.$watch('options.readonly', (newVal, oldVal) => {
                this.destroy();
                this.initForm(opts);
            });
        }
    };
</script>

<style>
    body > main > article {
        min-height: 68px;
    }
</style>



