<template>
    <div></div>
</template>

<script>
    /**
     * [EMAP]
     * @module EmapFileupload
     *
     * @fires add -data
     * @fires submit -data
     * @fires done -data
     * @fires fail -data
     *
     * @example
     *     <caption>html</caption>
     *     <emap-fileupload ftype='png'></emap-fileupload>
     */
    export default {
        /**
         * @property {String} contextPath 
         * @property {String} [token] token
         * @property {String} [storeId=file] 
         * @property {String/Array} ftype 
         * @property {Number} [size=2048] (Kb)
         * @property {Boolean} [newToken=true] token
         */
        props: {
            'contextPath': {
                type: String,
                default: ''
            },
            'token': {
                type: String,
                default: ''
            },
            'storeId': {
                type: String,
                default: 'file'
            },
            'ftype': {
                type: [Array, String],
                default () {
                    return [];
                }
            },
            'size': {
                type: Number,
                default: 2048
            },
            'newToken': {
                type: Boolean,
                default: true
            }
        },
        methods: {
            /**
             * token
             * @return {String} token
             */
            getFileToken () {
                return $(this.$el).emapFileUpload('getFileToken');
            },
            /**
             * tokenurl
             * @return {Array} url
             */
            getFileUrl () {
                return $(this.$el).emapFileUpload('getFileUrl');
            },
            /**
             * token
             * @return {Object} 
             */
            getFileData () {
                return $(this.$el).emapFileUpload('getFileData');
            },
            /**
             * 
             * @return {String} token
             */
            saveTempFile () {
                return $(this.$el).emapFileUpload('saveTempFile');
            },
            /**
             * 
             * @return {Number} 
             */
            getFileNum () {
                return $(this.$el).emapFileUpload('getFileNum');
            },
            /**
             * 
             */
            destroy () {
                return $(this.$el).emapFileUpload('destroy');
            }
        },
        ready () {
            var vm = this;
            var el = $(this.$el);

            var type = $.isArray(this.ftype) ? this.ftype : [this.ftype];

            el.emapFileUpload({
                contextPath: this.contextPath,
                storeId: this.storeId,
                type: type,
                token: this.token,
                size: this.size,
                newToken: this.newToken,
                add (e, data) {
                    vm.$dispatch('add', data);
                },
                submit (e, data) {
                    vm.$dispatch('submit', data);
                },
                done (e, data) {
                    vm.$dispatch('done', data);
                },
                fail (e, data) {
                    vm.$dispatch('fail', data);
                }
            });
        },
        beforeDestroy () {
            this.destroy();
        }
    };
</script>



