<template>
    <div></div>
</template>

<script>
    /**
     * [EMAP]
     * @module EmapImageupload
     *
     * @fires add -data
     * @fires submit -data
     * @fires done -data
     * @fires fail -data
     *
     * @example
     *     <caption>html</caption>
     *     <emap-imageupload :limit='3'></emap-imageupload>
     */
    export default {
        /**
         * @property {String} contextPath 
         * @property {String} [token] token
         * @property {String} [storeId=file] 
         * @property {String/Array} [ftype=['jpg', 'png', 'jpeg']] 
         * @property {Number} [size=2048] (Kb)
         * @property {Number} [limit=5] 
         * @property {String/Number} [width=200] 
         * @property {String/Number} [height=150] 
         * @property {Boolean} [multiple=false] 
         */
        props: {
            'contextPath': {
                type: String,
                default: ''
            },
            'token': {
                type: String,
                default: ''
            },
            'storeId': {
                type: String,
                default: 'file'
            },
            'ftype': {
                type: [Array, String],
                default () {
                    return ['jpg', 'png', 'jpeg'];
                }
            },
            'width': {
                default: 200
            },
            'height': {
                default: 150
            },
            'limit': {
                type: Number,
                default: 5
            },
            'size': {
                type: Number,
                default: 2048
            },
            'multiple': {
                type: Boolean,
                default: false
            }
        },
        methods: {
            /**
             * token
             * @return {String} token
             */
            getFileToken () {
                return $(this.$el).emapImageUpload('getFileToken');
            },
            /**
             * tokenurl
             * @return {Array} url
             */
            getFileUrl () {
                return $(this.$el).emapImageUpload('getFileUrl');
            },
            /**
             * 
             * @return {String} token
             */
            saveTempFile () {
                return $(this.$el).emapImageUpload('saveTempFile');
            },
            /**
             * 
             */
            destroy () {
                $(this.$el).emapImageUpload('destroy');
            }
        },
        ready () {
            var vm = this;
            var el = $(this.$el);

            var type = $.isArray(this.ftype) ? this.ftype : [this.ftype];

            el.emapImageUpload({
                contextPath: this.contextPath,
                storeId: this.storeId,
                type: type,
                token: this.token,
                size: this.size,
                limit: this.limit,
                width: this.width,
                height: this.height,
                multiple: this.multiple,
                add (e, data) {
                    vm.$dispatch('add', data);
                },
                submit (e, data) {
                    vm.$dispatch('submit', data);
                },
                done (e, data) {
                    vm.$dispatch('done', data);
                },
                fail (e, data) {
                    vm.$dispatch('fail', data);
                }
            });
        },
        beforeDestroy () {
            this.destroy();
        }
    };
</script>



