<template>
    <div></div>
</template>

<script>
    /**
     * [EMAP]
     * @module EmapAvatarupload
     *
     * @fires submit - tokentokenundefined
     *
     * @example
     *     <caption>html</caption>
     *     <emap-avatarupload ftype='png'></emap-avatarupload>
     */
    export default {
        /**
         * @property {String} contextPath 
         * @property {String} [token] token
         * @property {String} [storeId=file] id
         * @property {String/Array} [ftype] 
         * @property {Number} [size=2048]  Kb
         * @property {Number} [width=100] 
         * @property {Number} [ratio=1] 
         * @property {String} defaultAvatar 
         */
        props: {
            'contextPath': {
                type: String,
                default: ''
            },
            'token': {
                type: String,
                default: ''
            },
            'storeId': {
                type: String,
                default: 'file'
            },
            'ftype': {
                type: [Array, String],
                default () {
                    return [];
                }
            },
            'size': {
                type: Number,
                default: 2048
            },
            'width': {
                type: Number,
                default: 100
            },
            'ratio': {
                type: Number,
                default: 1
            },
            defaultAvatar: String
        },
        methods: {
            /**
             * token
             * @return {String} token
             */
            getFileToken () {
                return $(this.$el).emapAvatarUpload('getFileToken');
            },
            /**
             * tokenurl
             * @return {Array} url
             */
            getFileUrl () {
                return $(this.$el).emapAvatarUpload('getFileUrl');
            },
            /**
             * 
             * @return {String} token
             */
            saveTempFile () {
                return $(this.$el).emapAvatarUpload('saveTempFile');
            },
            /**
             * 
             */
            destroy () {
                $(this.$el).emapAvatarUpload('destroy');
            }
        },
        ready () {
            var vm = this;
            var el = $(this.$el);

            var type = $.isArray(this.ftype) ? this.ftype : [this.ftype];

            el.emapAvatarUpload({
                contextPath: this.contextPath,
                storeId: this.storeId,
                type: type,
                token: this.token,
                size: this.size,
                ratio: this.ratio,
                width: this.width,
                defaultAvatar: this.defaultAvatar,
                onSubmit (token) {
                    vm.$dispatch('submit', token);
                }
            });
        },
        beforeDestroy () {
            this.destroy();
        }
    };
</script>



