<template>
    <div></div>
</template>

<script>
    /**
     * 
     *
     * @module bhStepWizard
     * @fires change - stepId {stepId: "step3"}
     *
     * @example
     * <caption>html</caption>
     * <bh-step-wizard :items='items' current='step2'></bh-step-wizard>
     * <div class='clearfix'></div>
     * <div>
     *     <div id="step1" class="bh-hide">
     *         -1-
     *     </div>
     *     <div id="step2" class="bh-hide">
     *         -2-
     *     </div>
     *     <div id="step3" class="bh-hide">
     *         -3-
     *     </div>
     *     <div id="step4" class="bh-hide">
     *         -4-
     *     </div>
     *     <div id="step5" class="bh-hide">
     *         -5-
     *     </div>
     *     <div id="step6" class="bh-hide">
     *         -6-
     *     </div>
     * </div>
     * @example
     * <caption>javascript</caption>
     * export default {
     *     data () {
     *         return {
     *             items: [
     *                 {stepId: "step1", title: "-1"},
     *                 {stepId: "step2", title: "-2"},
     *                 {stepId: "step3", title: "-3"},
     *                 {stepId: "step4", title: "-4"},
     *                 {stepId: "step5", title: "-5"},
     *                 {stepId: "step6", title: "-6"}
     *             ]
     *         };
     *     },
     *     components: {BhStepWizard}
     * };
     */

    var pushAfterCheck = (item, arr) => {
        if (!arr || !item) {
            return;
        }

        if ($.inArray(item, arr) === -1) {
            arr.push(item);
        }
    };

    export default {
        /**
         * @property {Array} items  title,stepId,active,finished
         * @property {String} [current] stepId
         * @property {Array} [finished=[]] stepId
         * @property {Object} [container=$("body")] 
         * @property {Boolean} [clickable=true] 
         */
        props: {
            items: Array,
            current: String,
            container: Object,
            finished: {
                type: Array,
                default () {
                    return [];
                }
            },
            clickable: {
                type: Boolean,
                default: true
            }
        },
        methods: {
            /**
             * 
             */
            prev () {
                $(this.$el).bhStepWizard('activePrevItem');
            },
            /**
             * 
             */
            next () {
                $(this.$el).bhStepWizard('activeNextItem');
            },
            /**
             * 
             * @param  {String} stepId id
             */
            activate (stepId) {
                $(this.$el).bhStepWizard('changeToActive', stepId);
            },
            /**
             * 
             * @param  {String} stepId id
             */
            finish (stepId) {
                $(this.$el).bhStepWizard('changeToFinished', stepId);
                pushAfterCheck(stepId, this.finished);
            },
            /**
             * 
             * @return {Boolean} truefalse
             */
            isLast () {
                return $(this.$el).bhStepWizard('isLastStep');
            }
            // getFinishedIndexs () {
            //     return $(this.$el).bhStepWizard("getFinishedIndexs");
            // }
        },
        ready () {
            var self = this;
            var el = $(this.$el);

            el.bhStepWizard({
                items: self.items,
                active: self.current,
                container: self.container,
                finished: self.finished,
                isAddClickEvent: self.clickable,
                change (step) {
                    self.current = step.stepId;
                    self.$dispatch('change', step);
                }
            });
        }
    };
</script>



