<template>
    <div class="bh-tooltip">
        <header></header>
        <div class="content">
            <slot></slot>
        </div>
        <footer v-if='showFooter' @click='close' class='bh-text-caption bh-color-caption'>
            <div class='bh-pull-right'>{{closeText}}</div>
        </footer>
    </template>
</div>

<script>
    /**
     * 
     * @module BhTooltip
     * @example
     * <caption>html</caption>
     * <bh-tooltip :target='target'>
     *     <p>
     *         
     *     </p>
     *     <a href="#">
     *         html
     *     </a>
     * </bh-tooltip>
     */

    var initTooltip = (vm) => {
        if (vm.target) {
            $(vm.target).jqxTooltip({
                content: vm.$el,
                position: vm.position,
                autoHide: vm.autoHide,
                closeOnClick: vm.clickClose
            });
        }
    };

    export default {
        /**
         * @property {String} taget domjquery
         * @property {Boolean} [autoHide=true] 
         * @property {String} [position=default] top/bottom/left/right/top-left/bottom-left/top-right/bottom-right/mouse/mouseenter/default
         * @property {Boolean} [clickClose=false] 
         * @property {Boolean} [showFooter=false] 
         * @property {String} [closeText=] 
         */
        props: {
            target: {
                default: null
            },
            autoHide: {
                type: Boolean,
                default: true
            },
            position: {
                type: String,
                default: 'default'
            },
            clickClose: {
                type: Boolean,
                default: false
            },
            showFooter: {
                type: Boolean,
                default: false
            },
            closeText: {
                default: ''
            }
        },
        ready () {
            initTooltip(this);

            this.$watch('target', (newVal) => {
                initTooltip(this);
            });
        },
        beforeDestroy () {
            this.destroy();
        },
        methods: {
            /**
             * 
             */
            close () {
                if (this.target) {
                    $(this.target).jqxTooltip('close');
                }
            },
            destroy () {
                if (this.target) {
                    $(this.target).jqxTooltip('destroy');
                }
            }
        }
    };
</script>

<style scoped>
    footer > div {
        cursor: pointer;
    }
</style>


