<template>
    <div></div>
</template>

<script>
    /**
     * 
     * @module EmapDbTable
     *
     * @example
     *     <caption>html</caption>
     *     <emap-dd-table v-ref:ddt :options='options'></emap-dd-table>
     * @example
     *     <caption>javascript</caption>
     *     export default {
     *        data: function() {
     *            return {
     *                options: {
     *                    url: 'http://localhost:3000/mock/emap/code.json'
     *                }
     *            }
     *        },
     *        methods: {
     *            getVal: function() {
     *                console.log(this.$refs.ddt.getValue());
     *            }
     *        },
     *      components: {EmapDdTable}
     *     }
     */
    export default {
        /**
         * @property {Object} options 
         * @property {String} options.url url
         * @property {String} options.actionName 
         * @property {String} [options.valueMember=id] 
         * @property {String} [options.displayMember=name] 
         * @property {Array} [options.hideMember] 
         * @property {String} [options.width=300] 
         * @property {Function} [options.formatData] url
         */
        props: {
            options: Object
        },
        methods: {
            /**
             * 
             * @return {String}  id
             */
            getValue () {
                return $(this.$el).emapDropdownTable('getValue');
            },
            /**
             * 
             * @param {Array} val [value, descripton]value
             */
            setValue (val) {
                $(this.$el).emapDropdownTable('getValue', val);
            },
            /**
             * 
             */
            destroy () {
                $(this.$el).emapDropdownTable('destroy');
            }
        },
        ready () {
            var self = this;
            var el = $(self.$el);

            el.emapDropdownTable(self.options);
        },
        beforeDestory () {
            this.destroy();
        }
    };
</script>



