<template>
    <div></div>
</template>

<script>
    /**
     *  EMAPcallbacks
     * @module EmapEditor
     *
     * @example
     *     <caption>html</caption>
     *     <emap-editor :options='options'></emap-editor>
     * @example
     *     <caption>javascript</caption>
     *     export default {
     *         data: function() {
     *             return {
     *                 options: {
     *                     contextPath: 'mock'
     *                 }
     *             }
     *         },
     *         ready: function() {
     *         },
     *         components: {EmapEditor}
     *    }
     */

    export default {
        /**
         * @property {Object} options 
         * @property {String} options.contextPath 
         * @property {String} [options.placeholder] 
         * @property {Boolean} [options.fullFilePath=false] 
         * @property {Boolean} [options.dialogsInBody=false] body
         * @property {Boolean} [options.disableDragAndDrop=true] 
         */
        props: {
            options: Object
        },
        methods: {
            /**
             * 
             * @return {String} 
             */
            getValue () {
                return $(this.$el).emapEditor('getValue');
            },
            /**
             * 
             * @param {String} text 
             */
            setValue (text) {
                $(this.$el).emapEditor('setValue', text);
            },
            /**
             * 
             */
            enable () {
                $(this.$el).emapEditor('enable');
            },
            /**
             * 
             */
            disable () {
                $(this.$el).emapEditor('disable');
            },
            /**
             * 
             */
            clear () {
                $(this.$el).emapEditor('clear');
            },
            /**
             * 
             * @return {Boolean} truefalse
             */
            isEmpty () {
                return $(this.$el).emapEditor('isEmpty');
            }
        },
        ready () {
            $(this.$el).emapEditor(this.options);
        },
        beforeDestroy () {
            $(this.$el).emapEditor('destroy');
        }
    };
</script>

<style>
    .note-editor.panel-default > .panel-heading {
        color: #333;
        background-color: #f5f5f5;
        border-color: #ddd;
    }
    .note-editor .panel-heading {
        border-bottom: 1px solid transparent;
        border-top-left-radius: 3px;
        border-top-right-radius: 3px;
    }
</style>



