<template>
    <div class='bh-step-flow'>
        <slot></slot>
    </div>
</template>

<script>
    /**
     * 
     *
     * @module bhStepFlow
     *
     * @example
     * <caption>html</caption>
     * <bh-step-flow v-ref:sf>
     *     <bh-flow-node num='5' title='' :auto-hide='true'>
     *         <p></p>
     *         <p></p>
     *         <bh-button type='warning' @click.stop='hideAdd'></bh-button>
     *     </bh-flow-node>
     *     <bh-flow-node num='4' title='' caption='2015/12/26 09:00-10:00' tag=''>
     *         4\\
     *     </bh-flow-node>
     * </bh-step-flow>
     */

    export default {
        methods: {
            /**
             * 
             * @param  {String} num 
             */
            expand (num) {
                this.$broadcast('expand-flow-node', num);
            },
            /**
             * 
             * @param  {String} num 
             */
            collapse (num) {
                this.$broadcast('collapse-flow-node', num);
            },
            /**
             * 
             * @param  {String} num 
             */
            hide (num) {
                this.$broadcast('hide-flow-node', num);
            }
        },
        events: {
            'toggle-flow-node' (num, expanded) {
                if (expanded) {
                    this.expand(num);
                }
            }
        }
    };
</script>

<style lang="less">
    .bh-step-flow {
        .scenes-cbrt-record {
            &:first-child {
                &:before {
                    height: 0;
                }

                .scenes-cbrt-no {
                    margin-top: 6px;
                }
            }
        }
    }
</style>



