<template>
    <div></div>
</template>

<script>
    /**
     * 
     * @module EmapDdTree
     * @example
     *     <caption>html</caption>
     *     <!--  -->
     *     <emap-dd-tree :options='options1'></emap-dd-tree>
     *     <!--  -->
     *     <emap-dd-tree :options='options2'></emap-dd-tree>
     * @example
     *     <caption>javascript</caption>
     *     export default {
     *      data: function() {
     *          return {
     *              options1: {
     *                  checkboxes:true,
     *                  url: 'http://localhost:3000/mock/emap/ddtree.json',
     *                  params: {}
     *              },
     *              options2: {
     *                  checkboxes:true,
     *                  datas: [{
     *                      "id": "1",
     *                      "name": "",
     *                      "pId": "0"
     *                  }, {
     *                      "id": "000010",
     *                      "name": "\/",
     *                      "pId": "1"
     *                  }, {
     *                      "id": "000012",
     *                      "name": "\/",
     *                      "pId": "1"
     *                  }]
     *              }
     *          }
     *      },
     *      components: {EmapDdTree}
     *  }
     */
    export default {
        /**
         * @property {Object} options 
         * @property {String} [options.url] EMAP
         * @property {Object} [options.params] 
         * @property {Array} [options.datas] [{id:"",name:"",pId:""}]url
         * @property {Boolean} [options.checkboxes=false] 
         */
        props: {
            options: Object
        },
        methods: {
            /**
             * 
             * @return {String} id '000010,000012'
             */
            getValue () {
                return $(this.$el).emapDropdownTree('getValue');
            },
            /**
             * 
             * @return {String}  '\/, \/'
             */
            getText () {
                return $(this.$el).emapDropdownTree('getText');
            },
            /**
             * 
             * @param {Array} val [value, descripton] ['000010', '\/']
             */
            setValue (val) {
                $(this.$el).emapDropdownTree('setValue', val);
            },
            /**
             * 
             */
            disable () {
                $(this.$el).emapDropdownTree('disable');
            },
            /**
             * 
             */
            enable () {
                $(this.$el).emapDropdownTree('enable');
            }
        },
        ready () {
            $(this.$el).emapDropdownTree(this.options);
        }
    };
</script>



