<template>
    <div></div>
</template>

<script>
    /**
     * 
     * @module EmapSearch
     *
     * @fires search - params
     *
     * @example
     *     <caption>html</caption>
     *     <emap-search model-url='http://res.wisedu.com/fe_components/mock/advencedQueryModel.json' @search='search'></emap-search>
     * @example
     *     <caption>javascript</caption>
     *     export default {
     *         methods: {
     *             search: function(params) {
     *                 console.log(params);
     *             }
     *         },
     *         components: {EmapSearch}
     *     }
     */
    export default {
        /**
         * @property {String} modelUrl 
         * @property {String} modelName 
         * @property {String} [searchModel=easy]  'easy'/'advanced'
         * @property {Boolean} [schema=false] 
         * @property {Object} [defaultItem]  {"XM" : "11", "XH" : "123"}
         * @property {Boolean} [allowAllOption=true] buttonlist
         * @property {Boolean} [showTotalNum=false] 
         */
        props: {
            modelUrl: String,
            modelName: String,
            searchModel: {
                type: String,
                default: 'easy'
            },
            schema: {
                type: Boolean,
                default: false
            },
            defaultItem: Object,
            allowAllOption: {
                type: Boolean,
                default: true
            },
            showTotalNum: {
                type: Boolean,
                default: false
            }
        },
        ready () {
            var vm = this;
            var el = $(vm.$el);

            var searchData = WIS_EMAP_SERV.getModel(vm.modelUrl, vm.modelName, 'search');
            el.emapAdvancedQuery({
                data: searchData,
                searchModel: this.searchModel,
                schema: this.schema,
                defaultItem: this.defaultItem,
                allowAllOption: this.allowAllOption,
                showTotalNum: this.showTotalNum
            });

            el.on('search', (e, params, data) => {
                vm.$dispatch('search', params);
            });
        },
        methods: {
            /**
             * 
             * @return {String} 
             */
            getValue () {
                return $(this.$el).emapAdvancedQuery('getValue');
            },
            /**
             * 
             * @param {Object} data 
             */
            setValue (data) {
                $(this.$el).emapAdvancedQuery('setValue', data);
            },
            /**
             * 
             */
            clear () {
                $(this.$el).emapAdvancedQuery('clear');
            },
            /**
             * showTotalNum
             */
            updateTotalNum () {
                $(this.$el).emapAdvancedQuery('num');
            }
        },
        beforeDestroy () {
            var vm = this;
            var el = $(vm.$el);

            el.off('search');
        }
    };
</script>



